@import "https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap";
:root {
  --textColor: #3c484e;
  --shadowColor: 39, 44, 49;
}

body {
  color: var(--textColor);
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
}

.Main {
  grid-template-columns: minmax(auto, 768px);
  justify-content: center;
  padding: 10px;
  display: grid;
}

.Items {
  grid-gap: 1.5rem;
  grid-row-gap: 1.5em;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.Card {
  box-shadow: 8px 14px 38px rgba(var(--shadowColor), .06), 1px 3px 8px rgba(var(--shadowColor), .03);
  border-radius: 5px;
  margin: 0 0 20px;
  text-decoration: none;
  animation-name: fade;
  animation-duration: 4s;
  display: block;
}

.Card img {
  width: 100%;
  height: auto;
  border-radius: 5px 5px 0 0;
}

.Card h2 {
  justify-content: space-between;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: 300;
  display: flex;
}

@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

